.game {
    width: 100%;
    height: 100vh;
    background-image: url('../assets/img/game_background.png');
    background-size: cover;
}

.game > * {
    font-family: monospace;
}

.holesWrapper {
    width: fit-content;
    display: grid;
    column-gap: 100px;
    row-gap: 50px;
    margin: auto;
}

.holeWrapper {
    width: fit-content;
    height: 120px;
    position: relative;
    text-align: center;
}

.hole {
    width: 120px;
    height: 120px;
    background-color: #5C3317;
    border: 5px solid #3B240B;
    border-radius: 50%;
    position: absolute;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.5);
    color: white;
    transform: rotateX(60deg);
    z-index: 100;
    position: relative;
    bottom: -30%;
}

.imgWrapper {
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: absolute;
    left: 0;
    bottom: 10px;
    z-index: 500;
}

.baeverImg {
    width: 120px;
    user-select: none;
    position: absolute;
    left: 0;
    animation: qunduz 1.5s ease-in-out 1;
    z-index: 600;
}

.modal {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 900;
    background-image: url('../assets/img/game_background.png');
    background-size: cover;
}

.modalContent {
    width: 800px;
    height: 500px;
    background-color: #d8861a;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    border: 4px solid rgb(136, 73, 0);
    border-radius: 30px;
}

.modal h1 {
    font-size: 60px;
    margin-bottom: 20px;
    font-weight: 900;
    color: white;
}

.modal button {
    background-color: #945400;
    color: white;
    border: none;
    /* padding: 10px 20px; */
    width: 250px;
    height: 70px;
    border: 3px solid rgb(148, 79, 1);
    border-radius: 35px;
    cursor: pointer;
    font-size: 22px;
    transition: background-color 0.3s ease;
    margin-top: 20px;
    margin-left: 15px;
    box-shadow: 0 0 10px rgba(21, 21, 21, 0.8);
    font-weight: 600;
}

.modal button:hover {
    background-color: #a76610;
}

.modal button:active {
    background-color: #ca8d3d;
}

.statusWrapper {
    display: flex;
    justify-content: center;
    gap: 200px;
    padding-top: 80px;
}

.duration {
    width: 230px;
    height: 40px;
    display: flex;
    align-items: center;
    font-size: 20px;
    border: 3px solid #3B240B;
    background-color: #5C3317;
    border-radius: 8px;
    position: relative;
    overflow: hidden;
    color: #ffc42f;
}

.duration h1 {
    margin-left: 50px;
}

.duration h1,
i {
    z-index: 100;
}

.timeRunner {
    background-color: green;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    transition: 1.2s ease width;
}

.score {
    width: 200px;
    height: 40px;
    border: 3px solid #3B240B;
    background-color: #5C3317;
    color: #ffc42f;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    font-size: 18px;
}

@keyframes qunduz {
    0% {
        top: 100%;
    }

    50% {
        top: 0;
    }

    100% {
        top: 100%;
    }
}