.mainMenu {
    width: 100%;
    height: 100vh;
    background-image: url('./assets/img/lobby_background.jpg');
    background-size: cover;
    background-position: 0 -150px;
    background-repeat: no-repeat;
}

.button {
    width: 200px;
    height: 80px;
    border: 1px solid black;
    border-radius: 15px;
    font-size: 25px;
    background-color: #f2cb1e;
    color: white;
    cursor: pointer;
    position: absolute;
    top: 500px;
    transition: 0.3s ease transform;
}

.button:active {
    transform: scale(0.9);
}

.startButton {
    left: 590px;
}

.settingsButton {
    left: 800px;
}