.settings {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.settingsContent {
    width: 700px;
    height: 500px;
    background-color: #e59327;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.2);
    position: relative;
    border-radius: 5px;
    border-radius: 15px;
    text-align: center;
    color: #4a3f35;
    padding-top: 10px;
}

.settingsItemButton {
    width: 110px;
    height: 60px;
    position: absolute;
    right: -110px;
    border-radius: 0 10px 10px 0;
    cursor: pointer;
    transition: 0.3s ease all;
    border: none;
    font-size: 22px;
    padding: 5px 10px;
    font-weight: 600;
}

.settingsItemButton:hover {
    background-color: rgb(126, 126, 126);
}

.exitSettingsButton {
    left: -110px;
    width: 110px;
    height: 60px;
    position: absolute;
    right: -100px;
    border-radius: 10px 0 0 10px;
    cursor: pointer;
    transition: 0.3s ease all;
    border: none;
    top: 20px;
    font-size: 22px;
    font-weight: 600;
}

.exitSettingsButton:hover {
    background-color: black;
    color: white;
}

.timeSettingsButton {
    top: 20px;
}

.holeSettingsButton {
    top: 100px;
}

.holesNumberWrapper {
    display: flex;
    justify-content: center;
    gap: 20px;
    cursor: pointer;
    margin-top: 20px;
}

.holesNumber {
    width: 70px;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 18px;
    background: linear-gradient(45deg, #ff7e5f, #feb47b);
    color: black;
    transition: all 0.3s ease;
}

.holesNumber:hover {
    background: linear-gradient(45deg, #feb47b, #ff7e5f);
    transform: translateY(-3px);
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
}

select {
    width: 200px;
    padding: 10px;
    font-size: 16px;
    border: 2px solid #feb47b;
    border-radius: 8px;
    outline: none;
    transition: all 0.3s ease;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    margin-top: 20px;
}

select:focus {
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2);
    background-color: #fff9f4;
}